body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/**
 * RESET
 * Reference: http://meyerweb.com/eric/tools/css/reset/
 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

body {
  line-height: 1.618;
  font-family: Arial, Helvetica, sans-serif; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

code, pre, samp {
  font-family: monospace; }

/**
 * BASIC TYPE
 */
img, video {
  max-width: 100%;
  vertical-align: middle; }

p:not(:last-child) {
  margin: 0 0 1em; }

h1, h2, h3, h4 {
  margin: .5em 0 .75em;
  line-height: 1.3; }

h1 {
  font-size: 1.5em; }

h2 {
  font-size: 1.25em; }

h3 {
  font-size: 1.125em; }

h4 {
  font-size: 1em; }

/**
 * BOX HACKING
 */
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.wrapper {
  margin-right: auto;
  margin-left: auto;
  padding: 1.5em; }

body {
  background-color: #cccccc;
  color: #333333; }

.masonry-header {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1em;
  font-weight: bold;
  color: Navy;
  text-shadow: 1px 1px #222;
  text-align: center; }

.masonry-header-sub {
  font-size: .8rem;
  color: MidnightBlue; }

.masonry-wrapper {
  padding-left: 1em;
  padding-right: 1em;
  margin-right: auto;
  margin-left: auto; }

.masonry {
  -webkit-columns: 1;
          columns: 1;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
          column-gap: 15px; }

.masonry-item {
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  display: inline-block;
  vertical-align: top;
  margin-bottom: 15px;
  -webkit-filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.3));
          filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.3));
  transition: -webkit-filter .25s ease-in-out;
  transition: filter .25s ease-in-out;
  transition: filter .25s ease-in-out, -webkit-filter .25s ease-in-out;
  color: #111111;
  background-color: #f9f9f9; }

@media only screen and (min-width: 400px) and (max-width: 650px) {
  .masonry {
    -webkit-columns: 2;
            columns: 2; } }

@media only screen and (min-width: 651px) and (max-width: 900px) {
  .masonry {
    -webkit-columns: 3;
            columns: 3; } }

@media only screen and (min-width: 901px) and (max-width: 1150px) {
  .masonry {
    -webkit-columns: 4;
            columns: 4; } }

@media only screen and (min-width: 1151px) and (max-width: 1400px) {
  .masonry {
    -webkit-columns: 5;
            columns: 5; } }

@media only screen and (min-width: 1401px) {
  .masonry {
    -webkit-columns: 6;
            columns: 6; } }

.masonry-item, .masonry-content {
  border-radius: 4px;
  overflow: hidden; }

.masonry-item:hover {
  -webkit-filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.3));
          filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.3)); }

.masonry-nb {
  font-size: .7rem;
  font-style: italic;
  color: black;
  background-color: Pink;
  border-radius: 4px;
  margin-left: 5px;
  padding: 5px; }

.masonry-content {
  overflow: hidden; }

.masonry-img {
  transition: all .3s linear 0s; }

.masonry-img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: .6; }

.masonry-text {
  transition: all .3s linear 0s; }

.masonry-text:hover {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  background-color: LemonChiffon;
  box-shadow: 2px 2px 8px 0px black; }

.masonry-title, .masonry-description {
  margin: 0; }

.masonry-title {
  font-weight: 700;
  font-size: 1.1rem;
  padding: 1rem 1.5rem; }

.masonry-description {
  padding: 1.5rem;
  font-size: .75rem;
  border-top: 1px solid rgba(0, 0, 0, 0.05); }

.masonry-footer {
  font-size: .75em;
  opacity: .5;
  text-align: center;
  padding-top: 3em;
  padding-bottom: 3em;
  margin-bottom: -1.5em;
  transition: opacity 1s ease-in-out; }

.masonry-footer a {
  color: Navy; }

.masonry-footer:hover, .masonry-footer:active, .masonry-footer:focus {
  opacity: 1; }

.date-stamp {
  position: absolute;
  top: 15px;
  right: 15px;
  background-opacity: .5;
  width: 55px;
  height: 55px;
  padding: 12.5px 0;
  border-radius: 100%;
  color: #ffffff;
  font-weight: 700;
  text-align: center;
  text-shadow: 1px 1px 2px black;
  box-sizing: border-box;
  box-shadow: 1px 1px 4px 0px black; }
  .date-stamp .date-mon-dd {
    font-size: 12px; }
  .date-stamp .date-year {
    font-size: 10px; }

